 /* Global Styles */
body {
  margin: 0;
  padding: 0;
  font-family: 'Arial', sans-serif;
  background-color: #9b51e0,rgb(216, 52, 161),rgb(62, 162, 188);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

/* App container */
.app {
  background:linear-gradient(83deg,#f71aa2 0%,#6800ff 80%);
  padding: 20px;
  border-radius: 10px;
  box-shadow:0 0 50px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 500px;
  text-align: center;
}

/* Heading style */
.heading {
  font-size:5em;
  color: #f70000;
  margin-bottom: 20px;
  text-align: center;
}

/* Question section */
.question-section {
  margin-top: 20px;
}

.question-count {
  font-size: 1.2em;
  color: #444;
  margin-bottom: 10px;
}

.question-text {
  font-size: 1.5em;
  font-weight: bold;
  margin-bottom: 20px;
  color: #333;
}

/* Answer section */
.answer-section {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 20px;
}

/* Button styling */
button {
  padding: 10px;
  font-size: 1.1em;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #0056b3;
}

/* Score section */
.score-section {
  font-size: 1.5em;
  font-weight: bold;
  color: #333;
  margin-top: 20px;
}

/* Responsive Design */
@media (max-width: 600px) {
  .heading {
    font-size: 1.6em;
  }

  .question-text {
    font-size: 1.2em;
  }

  button {
    font-size: 1em;
  }
}
